const naoProxy = {}
GetServices("10.50.16.47").then((session) => {
    naoProxy.session = session
    console.log('all services retrieved')
})

function GetServices(ipAddress) {
    return new Promise((resolve, reject) => {
        console.log(`connecting to ${ipAddress}...`)
        // const session = new QiSession()
        const session = new QiSession(ipAddress)
        session.socket()
            .on('connect', onSessionConnect(session, resolve))
            .on('disconnect', console.log('disconnected..'))
    })

    function onSessionConnect(session, callback) {
        console.log('QiSession connected!')

        resolve()
    }
}




export default (url, body) => {

    console.log('Socket request made')
    console.log(url)
    console.log(body)
    return new Promise((resolve, reject) => {



    })
}


